'use client'

import { useState, useCallback } from 'react'
import { useDropzone } from 'react-dropzone'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Badge } from '@/components/ui/badge'
import { Alert, AlertDescription } from '@/components/ui/alert'
import { Progress } from '@/components/ui/progress'
import { Separator } from '@/components/ui/separator'
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table'
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog'
import { Textarea } from '@/components/ui/textarea'
import Link from 'next/link'
import { useForm } from 'react-hook-form'
import { zodResolver } from '@hookform/resolvers/zod'
import { z } from 'zod'

const mediaUploadSchema = z.object({
  caption: z.string().optional(),
})

type MediaUploadForm = z.infer<typeof mediaUploadSchema>

interface MediaAsset {
  id: string
  filename: string
  type: 'IMAGE' | 'VIDEO' | 'DOCUMENT'
  url: string
  size: number
  providerMediaId?: string
  metadata?: {
    width?: number
    height?: number
    duration?: number
  }
  createdAt: string
}

// Mock data for demonstration
const mockMediaAssets: MediaAsset[] = [
  {
    id: '1',
    filename: 'product-showcase.jpg',
    type: 'IMAGE',
    url: 'https://storage.googleapis.com/workspace-0f70711f-8b4e-4d94-86f1-2a93ccde5887/image/fb3d9b32-7a49-4e40-8682-324c1778ca0f.png',
    size: 2048576, // 2MB
    providerMediaId: 'wa_media_123',
    metadata: { width: 1920, height: 1080 },
    createdAt: '2024-01-15T10:00:00Z',
  },
  {
    id: '2',
    filename: 'demo-video.mp4',
    type: 'VIDEO',
    url: 'https://storage.googleapis.com/workspace-0f70711f-8b4e-4d94-86f1-2a93ccde5887/image/9650d154-5140-4d00-9f8a-f1a501d31bd0.png',
    size: 15728640, // 15MB
    providerMediaId: 'wa_media_456',
    metadata: { width: 1280, height: 720, duration: 45 },
    createdAt: '2024-01-16T14:30:00Z',
  },
  {
    id: '3',
    filename: 'holiday-banner.png',
    type: 'IMAGE',
    url: 'https://storage.googleapis.com/workspace-0f70711f-8b4e-4d94-86f1-2a93ccde5887/image/b12f5ddd-9727-4223-9e99-c768eacb8403.png',
    size: 1536000, // 1.5MB
    providerMediaId: 'wa_media_789',
    metadata: { width: 1200, height: 400 },
    createdAt: '2024-01-17T09:15:00Z',
  },
]

export default function MediaPage() {
  const [uploadedFiles, setUploadedFiles] = useState<File[]>([])
  const [uploadProgress, setUploadProgress] = useState<{ [key: string]: number }>({})
  const [isUploading, setIsUploading] = useState(false)
  const [selectedMedia, setSelectedMedia] = useState<MediaAsset | null>(null)
  const [isPreviewOpen, setIsPreviewOpen] = useState(false)

  const {
    register,
    handleSubmit,
    formState: { errors, isSubmitting },
    reset,
  } = useForm<MediaUploadForm>({
    resolver: zodResolver(mediaUploadSchema),
  })

  const onDrop = useCallback((acceptedFiles: File[]) => {
    const validFiles = acceptedFiles.filter(file => {
      const isImage = file.type.startsWith('image/')
      const isVideo = file.type.startsWith('video/')
      const maxSize = isImage ? 5 * 1024 * 1024 : 16 * 1024 * 1024 // 5MB for images, 16MB for videos

      if (!isImage && !isVideo) {
        alert(`${file.name}: Only images and videos are supported`)
        return false
      }

      if (file.size > maxSize) {
        alert(`${file.name}: File size exceeds ${isImage ? '5MB' : '16MB'} limit`)
        return false
      }

      return true
    })

    setUploadedFiles(prev => [...prev, ...validFiles])
  }, [])

  const { getRootProps, getInputProps, isDragActive } = useDropzone({
    onDrop,
    accept: {
      'image/*': ['.jpg', '.jpeg', '.png', '.gif'],
      'video/*': ['.mp4', '.mov', '.avi'],
    },
    multiple: true,
  })

  const removeFile = (index: number) => {
    setUploadedFiles(prev => prev.filter((_, i) => i !== index))
  }

  const uploadFiles = async (data: MediaUploadForm) => {
    if (uploadedFiles.length === 0) return

    setIsUploading(true)
    const newProgress: { [key: string]: number } = {}

    try {
      for (let i = 0; i < uploadedFiles.length; i++) {
        const file = uploadedFiles[i]
        const fileKey = `${file.name}-${i}`
        newProgress[fileKey] = 0
        setUploadProgress({ ...newProgress })

        const formData = new FormData()
        formData.append('file', file)
        if (data.caption) {
          formData.append('caption', data.caption)
        }

        // Simulate upload progress
        const progressInterval = setInterval(() => {
          newProgress[fileKey] = Math.min(newProgress[fileKey] + 10, 90)
          setUploadProgress({ ...newProgress })
        }, 200)

        const response = await fetch('/api/media/upload', {
          method: 'POST',
          body: formData,
        })

        clearInterval(progressInterval)

        const result = await response.json()

        if (response.ok && result.success) {
          newProgress[fileKey] = 100
          setUploadProgress({ ...newProgress })
        } else {
          console.error('Upload error:', result.error)
          newProgress[fileKey] = -1 // Error state
          setUploadProgress({ ...newProgress })
        }
      }

      // Clear uploaded files after successful upload
      setTimeout(() => {
        setUploadedFiles([])
        setUploadProgress({})
        reset()
      }, 2000)

    } catch (error) {
      console.error('Upload error:', error)
    } finally {
      setIsUploading(false)
    }
  }

  const formatFileSize = (bytes: number) => {
    if (bytes === 0) return '0 Bytes'
    const k = 1024
    const sizes = ['Bytes', 'KB', 'MB', 'GB']
    const i = Math.floor(Math.log(bytes) / Math.log(k))
    return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i]
  }

  const getMediaTypeIcon = (type: string) => {
    switch (type) {
      case 'IMAGE':
        return 'üñºÔ∏è'
      case 'VIDEO':
        return 'üé•'
      case 'DOCUMENT':
        return 'üìÑ'
      default:
        return 'üìÅ'
    }
  }

  const openPreview = (media: MediaAsset) => {
    setSelectedMedia(media)
    setIsPreviewOpen(true)
  }

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <header className="bg-white border-b border-gray-200">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center h-16">
            <div className="flex items-center">
              <Link href="/" className="text-2xl font-bold text-gray-900">
                WhatsApp Campaign Manager
              </Link>
            </div>
            <Link href="/">
              <Button variant="ghost">Back to Dashboard</Button>
            </Link>
          </div>
        </div>
      </header>

      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <div className="mb-8">
          <h1 className="text-3xl font-bold text-gray-900 mb-4">Media Library</h1>
          <p className="text-lg text-gray-600">
            Upload and manage images and videos for your WhatsApp campaigns
          </p>
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
          {/* Upload Section */}
          <div className="lg:col-span-2 space-y-6">
            <Card>
              <CardHeader>
                <CardTitle>Upload Media</CardTitle>
                <CardDescription>
                  Upload images (‚â§5MB) and videos (‚â§16MB) for your campaigns
                </CardDescription>
              </CardHeader>
              <CardContent className="space-y-6">
                {/* File Drop Zone */}
                <div
                  {...getRootProps()}
                  className={`border-2 border-dashed rounded-lg p-8 text-center cursor-pointer transition-colors ${
                    isDragActive
                      ? 'border-blue-400 bg-blue-50'
                      : 'border-gray-300 hover:border-gray-400'
                  }`}
                >
                  <input {...getInputProps()} />
                  <div className="space-y-4">
                    <div className="text-4xl">üìé</div>
                    <div>
                      <p className="text-lg font-medium">
                        {isDragActive
                          ? 'Drop the files here'
                          : 'Drag & drop media files here, or click to select'}
                      </p>
                      <p className="text-sm text-gray-600 mt-2">
                        Supports JPG, PNG, GIF, MP4, MOV, AVI
                      </p>
                    </div>
                  </div>
                </div>

                {/* Uploaded Files Preview */}
                {uploadedFiles.length > 0 && (
                  <div className="space-y-4">
                    <h3 className="font-medium">Files to Upload</h3>
                    <div className="space-y-2">
                      {uploadedFiles.map((file, index) => {
                        const fileKey = `${file.name}-${index}`
                        const progress = uploadProgress[fileKey] || 0
                        const isError = progress === -1

                        return (
                          <div key={index} className="flex items-center justify-between p-3 bg-gray-50 rounded-lg">
                            <div className="flex items-center space-x-3">
                              <div className="text-2xl">
                                {file.type.startsWith('image/') ? 'üñºÔ∏è' : 'üé•'}
                              </div>
                              <div>
                                <p className="font-medium">{file.name}</p>
                                <p className="text-sm text-gray-600">
                                  {formatFileSize(file.size)} ‚Ä¢ {file.type.split('/')[1].toUpperCase()}
                                </p>
                              </div>
                            </div>
                            <div className="flex items-center space-x-2">
                              {isUploading && (
                                <div className="w-24">
                                  <Progress 
                                    value={isError ? 0 : progress} 
                                    className={`h-2 ${isError ? 'bg-red-100' : ''}`}
                                  />
                                </div>
                              )}
                              {!isUploading && (
                                <Button
                                  variant="outline"
                                  size="sm"
                                  onClick={() => removeFile(index)}
                                >
                                  Remove
                                </Button>
                              )}
                            </div>
                          </div>
                        )
                      })}
                    </div>

                    <form onSubmit={handleSubmit(uploadFiles)} className="space-y-4">
                      <div>
                        <Label htmlFor="caption">Caption (Optional)</Label>
                        <Textarea
                          id="caption"
                          placeholder="Add a caption for your media files"
                          {...register('caption')}
                        />
                      </div>

                      <Button 
                        type="submit" 
                        disabled={isUploading || uploadedFiles.length === 0}
                        className="w-full"
                      >
                        {isUploading ? 'Uploading...' : `Upload ${uploadedFiles.length} File${uploadedFiles.length > 1 ? 's' : ''}`}
                      </Button>
                    </form>
                  </div>
                )}
              </CardContent>
            </Card>

            {/* Media Library */}
            <Card>
              <CardHeader>
                <CardTitle>Media Library</CardTitle>
                <CardDescription>
                  Your uploaded media files ready for campaigns
                </CardDescription>
              </CardHeader>
              <CardContent>
                {mockMediaAssets.length > 0 ? (
                  <div className="space-y-4">
                    {mockMediaAssets.map((media) => (
                      <div key={media.id} className="flex items-center justify-between p-4 border rounded-lg hover:bg-gray-50">
                        <div className="flex items-center space-x-4">
                          <div className="text-3xl">{getMediaTypeIcon(media.type)}</div>
                          <div>
                            <h3 className="font-medium">{media.filename}</h3>
                            <div className="flex items-center space-x-4 text-sm text-gray-600">
                              <span>{formatFileSize(media.size)}</span>
                              <Badge variant="secondary">{media.type}</Badge>
                              {media.metadata && (
                                <span>
                                  {media.metadata.width && media.metadata.height && 
                                    `${media.metadata.width}√ó${media.metadata.height}`}
                                  {media.metadata.duration && ` ‚Ä¢ ${media.metadata.duration}s`}
                                </span>
                              )}
                            </div>
                          </div>
                        </div>
                        <div className="flex items-center space-x-2">
                          {media.providerMediaId && (
                            <Badge variant="default">Uploaded to WhatsApp</Badge>
                          )}
                          <Button
                            variant="outline"
                            size="sm"
                            onClick={() => openPreview(media)}
                          >
                            Preview
                          </Button>
                        </div>
                      </div>
                    ))}
                  </div>
                ) : (
                  <div className="text-center py-8">
                    <div className="text-4xl mb-4">üìÅ</div>
                    <h3 className="text-lg font-medium text-gray-900 mb-2">No media files yet</h3>
                    <p className="text-gray-600">
                      Upload your first image or video to get started
                    </p>
                  </div>
                )}
              </CardContent>
            </Card>
          </div>

          {/* Instructions Sidebar */}
          <div className="space-y-6">
            <Card>
              <CardHeader>
                <CardTitle className="text-lg">Upload Guidelines</CardTitle>
              </CardHeader>
              <CardContent className="space-y-4 text-sm">
                <div>
                  <h4 className="font-medium mb-2">Image Requirements</h4>
                  <ul className="space-y-1 text-gray-600">
                    <li>‚Ä¢ Maximum size: 5MB</li>
                    <li>‚Ä¢ Formats: JPG, PNG, GIF</li>
                    <li>‚Ä¢ Recommended: 1200√ó630px</li>
                    <li>‚Ä¢ Aspect ratio: 16:9 or 1:1</li>
                  </ul>
                </div>

                <Separator />

                <div>
                  <h4 className="font-medium mb-2">Video Requirements</h4>
                  <ul className="space-y-1 text-gray-600">
                    <li>‚Ä¢ Maximum size: 16MB</li>
                    <li>‚Ä¢ Formats: MP4, MOV, AVI</li>
                    <li>‚Ä¢ Maximum duration: 30 seconds</li>
                    <li>‚Ä¢ Recommended: 1280√ó720px</li>
                  </ul>
                </div>

                <Separator />

                <div>
                  <h4 className="font-medium mb-2">Best Practices</h4>
                  <ul className="space-y-1 text-gray-600">
                    <li>‚Ä¢ Use high-quality images</li>
                    <li>‚Ä¢ Keep videos short and engaging</li>
                    <li>‚Ä¢ Add descriptive captions</li>
                    <li>‚Ä¢ Test on mobile devices</li>
                  </ul>
                </div>
              </CardContent>
            </Card>

            <Card>
              <CardHeader>
                <CardTitle className="text-lg">WhatsApp Integration</CardTitle>
              </CardHeader>
              <CardContent className="space-y-3 text-sm">
                <Alert>
                  <AlertDescription>
                    Media files are automatically uploaded to WhatsApp's servers when you create campaigns. 
                    This ensures fast delivery and compliance with WhatsApp's media policies.
                  </AlertDescription>
                </Alert>

                <div>
                  <h4 className="font-medium mb-2">Media Usage</h4>
                  <ul className="space-y-1 text-gray-600">
                    <li>‚Ä¢ Attach to campaign messages</li>
                    <li>‚Ä¢ Use in message templates</li>
                    <li>‚Ä¢ Send as standalone media</li>
                    <li>‚Ä¢ Include captions and context</li>
                  </ul>
                </div>
              </CardContent>
            </Card>
          </div>
        </div>

        {/* Media Preview Dialog */}
        <Dialog open={isPreviewOpen} onOpenChange={setIsPreviewOpen}>
          <DialogContent className="max-w-4xl">
            <DialogHeader>
              <DialogTitle>{selectedMedia?.filename}</DialogTitle>
              <DialogDescription>
                {selectedMedia?.type} ‚Ä¢ {selectedMedia && formatFileSize(selectedMedia.size)}
                {selectedMedia?.metadata && (
                  <>
                    {selectedMedia.metadata.width && selectedMedia.metadata.height && 
                      ` ‚Ä¢ ${selectedMedia.metadata.width}√ó${selectedMedia.metadata.height}`}
                    {selectedMedia.metadata.duration && ` ‚Ä¢ ${selectedMedia.metadata.duration}s`}
                  </>
                )}
              </DialogDescription>
            </DialogHeader>

            {selectedMedia && (
              <div className="space-y-4">
                <div className="flex justify-center">
                  {selectedMedia.type === 'IMAGE' ? (
                    <img
                      src={selectedMedia.url}
                      alt={selectedMedia.filename}
                      className="max-w-full max-h-96 object-contain rounded-lg"
                      onError={(e) => {
                        const target = e.target as HTMLImageElement;
                        target.src = 'https://storage.googleapis.com/workspace-0f70711f-8b4e-4d94-86f1-2a93ccde5887/image/8dc52303-b5a8-4252-923e-b99f0ac0911f.png';
                      }}
                    />
                  ) : selectedMedia.type === 'VIDEO' ? (
                    <div className="bg-gray-100 rounded-lg p-8 text-center">
                      <div className="text-4xl mb-4">üé•</div>
                      <p className="text-gray-600">Video preview not available</p>
                      <p className="text-sm text-gray-500 mt-2">
                        Video will be processed when used in campaigns
                      </p>
                    </div>
                  ) : (
                    <div className="bg-gray-100 rounded-lg p-8 text-center">
                      <div className="text-4xl mb-4">üìÑ</div>
                      <p className="text-gray-600">Document preview not available</p>
                    </div>
                  )}
                </div>

                <div className="grid grid-cols-2 gap-4 text-sm">
                  <div>
                    <Label className="text-gray-600">File Size</Label>
                    <p>{formatFileSize(selectedMedia.size)}</p>
                  </div>
                  <div>
                    <Label className="text-gray-600">Upload Date</Label>
                    <p>{new Date(selectedMedia.createdAt).toLocaleDateString()}</p>
                  </div>
                  <div>
                    <Label className="text-gray-600">WhatsApp Status</Label>
                    <p>
                      {selectedMedia.providerMediaId ? (
                        <Badge variant="default">Uploaded</Badge>
                      ) : (
                        <Badge variant="secondary">Pending</Badge>
                      )}
                    </p>
                  </div>
                  <div>
                    <Label className="text-gray-600">Media ID</Label>
                    <p className="font-mono text-xs">
                      {selectedMedia.providerMediaId || 'Not assigned'}
                    </p>
                  </div>
                </div>
              </div>
            )}
          </DialogContent>
        </Dialog>
      </main>
    </div>
  )
}
